"use client";

import { useMemo } from "react";
import ClassCard from "@/components/athlete/ClassCard";
import { mockClasses } from "@/lib/mockClasses";
import { useAthlete } from "@/components/athlete/AthleteStore";
import Link from "next/link";


export default function ReservasPage() {
  const { bookedIds, toggleBooking } = useAthlete();

  const bookedCountById = useMemo(
    () => ({ c1: 10, c2: 12, c3: 7 }),
    []
  );

  return (
    <div>
      <div className="flex items-end justify-between">
        <div>
          <h1 className="text-xl font-semibold">Reservar</h1>
          <p className="mt-1 text-sm text-gray-500">
            Elegí una clase y reservá tu lugar.
          </p>
        </div>
        <Link
  href="/athlete/mis-reservas"
  className="text-sm text-gray-600 underline"
>
  Mis reservas
</Link>

      </div>

      <div className="mt-4 grid gap-3">
        {mockClasses.map((c) => (
          <ClassCard
            key={c.id}
            item={c}
            isBooked={bookedIds.includes(c.id)}
            bookedCount={bookedCountById[c.id] ?? 0}
            onToggle={toggleBooking}
          />
        ))}
      </div>
    </div>
  );
}
